import React, { useEffect } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import Game from './pages/Game';
import SelectTheme from './pages/SelectTheme/SelectTheme2';

export default function Routes() {

  useEffect(() => {
    window.history.pushState("nohb", null, "");
  }, []);

  return (
    <BrowserRouter>
      <Switch>
        <Route path="/sevengalaxies-app/game" exact component={Game} />
        <Route path="/sevengalaxies-app/"
          render={(props) => (
            <SelectTheme escolheTheme={true} modal={false} />)}
        // component={SelectTheme()} 
        />

        {/* <Route path="/sevengalaxies-app/" exact component={Menu}/> */}
        {/* <Route path="/sevengalaxies-app/Game" component={Game}/> */}
      </Switch>
    </BrowserRouter>

  )



}
