import React from 'react';
import './Menu.css';

function Menu(props) {

  const localTheme = window.localStorage.getItem("sevengalaxies@theme");


  return (
    <div className={"menu" + " " + localTheme + "-menu"} >
      <div className="menu-container">


        <p>Menu</p>

        <div className="menu-button b1" onClick={() => props.handleOption('novo')}>
          NOVA PARTIDA
        </div>

        <div className="menu-button b2" onClick={() => props.handleOption('tema')}>
          ALTERAR TEMA
        </div>

        <div className="menu-button b3" onClick={() => props.handleOption('sobre')}>
          SOBRE
        </div>

      </div>
    </div>
  )
}

export default Menu;