import React, { useState } from 'react';
import { FiMenu } from 'react-icons/fi';
import './Toolbar.css';

import SelectTheme from '../pages/SelectTheme/SelectTheme2';
import SetSessionMatch from '../tools/SetSessionMatch';
import Menu from '../pages/Menu';

function Toolbar(props) {
  const [escolheTheme, setEscolheTheme] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  // useState(() => {
  //   const localTheme = window.localStorage.getItem("theme");
  //   console.log(localTheme);
  //   if (localTheme)
  //     return false;
  //   else
  //     return true;

  // });


  function OpenMenu() {
    // window.location.assign('#theme');
    // setEscolheTheme(true);
    setMenuOpen(menuOpen ? false : true);
  }

  function HandleOption(escolha) {
    // console.log(escolha);
    if (escolha === 'tema')
      setEscolheTheme(true);
    if (escolha === 'novo') {
      SetSessionMatch({}, {}, true);
      props.refresh();
    }
    OpenMenu();
  }

  return (
    <div className="toolbar" >
      <div className="content-toolbar">
        {/* <FiMenu
          className="menu"
          onClick={() => OpenMenu()} /> */}

        <div className={menuOpen ? "burger-menu open" : "burger-menu"}
          onClick={() => OpenMenu()} >
          <div className="bar1" key="b1" />
          <div className="bar2" key="b2" />
          <div className="bar3" key="b3" />
        </div>
        {menuOpen ?
          // <div className="dropdown">
          //   <div className="menu">
          //     <a href="#theme" className="menu-item" onClick={() => setEscolheTheme(true)}>
          //       {/* <span className="icon-button"><FiMenu /></span> */}
          //     Alterar Tema
          //   </a>
          //     <a href="#game" className="menu-item" onClick={() => setEscolheTheme(true)}>
          //       {/* <span className="icon-button"><FiMenu /></span> */}
          //     Nova Partida
          //   </a>
          //     <a href="#about" className="menu-item" onClick={() => setEscolheTheme(true)}>
          //       {/* <span className="icon-button"><FiMenu /></span> */}
          //     Sobre
          //   </a>
          //   </div>
          // </div>
          <Menu handleOption={HandleOption} />
          : null}


        <SelectTheme
          onClose={() => { setEscolheTheme(false); setMenuOpen(false); }}
          escolheTheme={escolheTheme}
          modal={true} />

      </div>

    </div>
  );
}

